import { createWebHistory, createRouter } from 'vue-router'
import Login from './components/Login.vue'
import Main from './components/Main.vue'

const routes = [
  {
      path: '/',
      name: 'Main',
      component: Main
  },
  {
      path: '/login',
      name: 'Login',
      component: Login
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router